import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    Createsocial_intereststrData: null,
  errors:[]
};

export const Createsocial_intereststSlice = createSlice({
    name: 'Createsocial_intereststSlice',
    initialState,
    reducers: {
        Createsocial_intereststSliceInfo: (state, action) => {
        // Redux Toolkit allows us to write "mutating" logic in reducers. It
        // doesn't actually mutate the state because it uses the Immer library,
        // which detects changes to a "draft state" and produces a brand new
        // immutable state based off those changes
        state.Createsocial_intereststrData = action.payload;
        console.log(state.Createsocial_intereststrData);
      },
      errors: (state, action) => {
          // Redux Toolkit allows us to write "mutating" logic in reducers. It
          // doesn't actually mutate the state because it uses the Immer library,
          // which detects changes to a "draft state" and produces a brand new
          // immutable state based off those changes
          state.errors = action.payload;
        },
    
    }
  });

  // Action creators are generated for each case reducer function
export const { Createsocial_intereststSliceInfo ,errors} = Createsocial_intereststSlice.actions;

export default Createsocial_intereststSlice.reducer;