import { createSlice } from '@reduxjs/toolkit';

const initialState = {
   EditeCarePlanData: null,
  errors:[]
};

export const EditeCarePlanDataSlice = createSlice({
  name: 'EditeCarePlanDataSlice',
  initialState,
  reducers: {
    EditeCarePlanDataSliceInfo: (state, action) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.EditeCarePlanData = action.payload;
      console.log(state.EditeCarePlanData);
    },
    errors: (state, action) => {
        // Redux Toolkit allows us to write "mutating" logic in reducers. It
        // doesn't actually mutate the state because it uses the Immer library,
        // which detects changes to a "draft state" and produces a brand new
        // immutable state based off those changes
        state.errors = action.payload;
      },
    
  },

  


});

// Action creators are generated for each case reducer function
export const { EditeCarePlanDataSliceInfo ,errors} = EditeCarePlanDataSlice.actions;

export default EditeCarePlanDataSlice.reducer;